import React, { Component } from "react";
import SubNavbar from "../SubNavbar";
import Navbar from "../Navbar";
import axios from "axios";
import { Container, Row } from "reactstrap";

class AllProducts extends Component {
  state = {
    persons: [],
  };

  componentDidMount() {
    axios.get("/assets/js/Api.json").then((element) => {
      this.setState({
        persons: element.data.products,
      });
    });
  }

  render() {
    return (
      <div>
        <SubNavbar />
        <Navbar />
        <div className="section properties">
          <Container>
            <Row className="properties-box">
            {this.state.persons.map((item) => (
              <div className="col-lg-4 col-md-6 align-self-center mb-30 properties-items col-md-6 adv">
                <div className="item">
                  <a href="property-details.html">
                    <img src={item.images[0]} alt="" style={{height:"350px"}} />
                  </a>
                  <span className="category">{item.title}</span>
                  <h6>${item.price}</h6>
                  <h4>
                    <a href="property-details.html">
                      {item.category}
                    </a>
                  </h4>
                  <ul>
                    <li>
                    rating: <span>{item.rating}</span>
                    </li>
                  
                  </ul>
                  <div className="main-button">
                    <a href="property-details.html">Schedule a visit</a>
                  </div>
                </div>
              </div>
            ))}
            </Row>

          </Container>

        </div>
      </div>
    );
  }
}
export default AllProducts;
