import React from 'react'
import SubNavbar from '../SubNavbar';
import Navbar from '../Navbar';
import { CartProvider, useCart } from 'react-use-cart';

function Cart() {
    const { isEmpty, totalUniqueItems, items, updateItemQuantity, removeItem, cartTotal,totalItems} =
      useCart();
  
    if (isEmpty) {
      return <h2 className="text-center">Your cart is empty</h2>;
    }
    return (
      <div>
        <main class="page">
          <section class="shopping-cart dark">
            <div class="container">
              <div class="block-heading">
                <h2>Shopping Cart {(totalUniqueItems)}</h2>
               
              </div>
              <div class="content">
                <div class="row">
                  <div class="col-md-12 col-lg-8">
                    <div class="items">
                      {items.map( (element)=>
                      
                      <div class="product">
                        <div class="row">
                          <div class="col-md-3">
                            <img
                              class="img-fluid mx-auto d-block image"
                              src={element.images[0]}
                            />
                          </div>
                          <div class="col-md-8">
                            <div class="info">
                              <div class="row">
                                <div class="col-md-5 product-name">
                                  <div class="product-name">
                                    <a href="#">{element.title}</a>
                                    <div class="product-info">
                                      <div>
                                        Display: <span class="value">5 inch</span>
                                      </div>
                                     
                                    </div>
                                  </div>
                                </div>
                                <div class="col-md-4 quantity">
                                  <label for="quantity">Quantity:</label>
                                  <br></br>
                                  <span>{element.quantity}</span>
                                  <br></br>
                                  <button className="btn btn-warning"  onClick={()=>updateItemQuantity(element.id,element.quantity-1)}>-</button>
  
                                  <button className="btn btn-success mx-2"  onClick={()=>updateItemQuantity(element.id,element.quantity+1)}>+</button>
                                  <button className="btn btn-danger" onClick={()=>removeItem(element.id)}>x</button>
                                </div>
                                <div class="col-md-3 price">
                                  <span>${Math.round(element.price*element.quantity)}</span>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                     
                      
                      )}
                      
                    </div>
                  </div>
                  <div class="col-md-12 col-lg-4">
                    <div class="summary">
                      <h3>Summary</h3>
                      <div class="summary-item">
                        <span class="text">Subtotal</span>
                        <span class="price">${Math.round(cartTotal)}</span>
                      </div>
                      <div class="summary-item">
                        <span class="text">Discount</span>
                        <span class="price">$0</span>
                      </div>
                      <div class="summary-item">
                        <span class="text">Shipping</span>
                        <span class="price">$5</span>
                      </div>
                      <div class="summary-item">
                        <span class="text">Total</span>
                        <span class="price">${Math.round(cartTotal+5)}</span>
                      </div>
                      <button
                        type="button"
                        class="btn btn-primary btn-lg btn-block"
                      >
                        Checkout
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </section>
        </main>
      </div>
    );
  }

function AllCart() {
  return (
    <CartProvider>
        <SubNavbar/>
        <Navbar/>
        <Cart/>
    </CartProvider>
  )
}

export default AllCart