import { CartProvider, useCart } from "react-use-cart";
import SubNavbar from "../SubNavbar";
import Navbar from "../Navbar";
import DataApi from "./DataApi";
import { Container, Row } from "reactstrap";
function Page() {
  const { addItem } = useCart();

  return (
    <div>
      <div className="section properties">
        <Container>
          <Row className="properties-box">
            {DataApi.map((item) => (
              <div className="col-lg-4 col-md-6 align-self-center mb-30 properties-items col-md-6 adv">
                <div className="item">
                  <a href="property-details.html">
                    <img
                      src={item.images[0]}
                      alt=""
                      style={{ height: "350px" }}
                    />
                  </a>
                  <span className="category">{item.title}</span>
                  <h6>${item.price}</h6>
                  <h4>
                    <a href="property-details.html">{item.category}</a>
                  </h4>
                  <ul>
                    <li>
                      rating: <span>{item.rating}</span>
                    </li>
                  </ul>
                  <div className="main-button">
                    <button onClick={() => addItem(item)}>Add To Cart</button>
                  </div>
                </div>
              </div>
            ))}
          </Row>
        </Container>
      </div>
    </div>
  );
}



function AllShop() {
  return (
    <CartProvider>
      <SubNavbar />
      <Navbar />
      <Page />
    
    </CartProvider>
  );
}
export default AllShop;
